<template>
  <div class="panoramicAnalysis">
    <div class="top">
      <div class="content content-border">
        <div class="content-title">
          <span class="block"></span>
          <span>主题标签</span>
        </div>
        <div class="chart-main">
          <themeLabelList
            :labelData="labelData"
            :pageConfig="labelPageConfig"
            @changePage="changePage"
            :showDetailData="labelDetailData"
            @clickDetail="getLabelDetailData"
            @changeDetailPage="changePage"
            @exportExcel="themeLabelExportExcel"
            :isQuery="true"
            ref="themeLabelList"
          ></themeLabelList>
        </div>
      </div>
    </div>
    <div class="center">
      <div class="content">
        <div class="content-left content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>收费渠道分布</span>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="chargingChannelRes"
              :barChartsData="chargingChannelData"
              :legendStatus="true"
              :isMultiple="true"
              :isShadow="true"
              :barWidth="20"
              :barType="'bar'"
              :name="'收费渠道分布'"
              :legendPosition="'right'"
              @setOption="setOption"
            ></barCharts>
          </div>
        </div>
        <div class="content-right content-border">
          <div class="content-title">
            <span class="block"></span>
            <span>核查异常规则Top10分布</span>
          </div>
          <div class="chart-main">
            <barCharts
              v-if="abnormalRulesRes"
              :barChartsData="abnormalRulesData"
              :name="'核查异常规则Top10分布'"
              :barWidth="20"
              :barType="'bar'"
              @setOption="setOption"
            ></barCharts>
          </div>
        </div>
      </div>
    </div>
    <div class="bottom">
      <div class="content content-border">
        <div class="content-title">
          <span class="block"></span>
          <span>回收率分布</span>
        </div>
        <div class="chart-main">
          <barCharts
            v-if="recoveryRes"
            :barChartsData="recoveryData"
            :legendStatus="true"
            :isMultiple="true"
            :barWidth="20"
            :barType="'bar'"
            :yAxiosDw="'%'"
            :name="'回收率分布'"
            :legendPosition="'right'"
            @setOption="setOption"
            :maxShowNumber="13"
          ></barCharts>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Interface from '@/base/mixin/fee-collection';
import barCharts from '@/components/bar-charts/bar-charts';
import themeLabelList from '@/components/theme-label-list/theme-label-list'
import { exportExcelGet } from "@/utils/export";
import {CP_PATH} from "@/config"
import axios from "axios";

export default {
  components: {
    barCharts,
    themeLabelList
  },
  name: "panoramicAnalysis",
  mixins: [Interface],
  data() {
    return {
      //关注标签
      labelData: [],

      labelQueryForm: {
        menuType: '6',
        current: 1,
        size: 10,
      },
      //分页设置
      labelPageConfig: {
        name: "label",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //标签详情
      labelDetailData: {},
      labelDetailQueryForm: {
        dbcolumnAS: "",
        menuType: '6',
        tagName: "",
        current: 1,
        size: 10,
      },
      //分页设置
      labelDetailPageConfig: {
        name: "labelDetail",
        current: 1,
        size: 10,
        pageSizes: [10, 20, 30, 40, 50],
        layout: "prev, pager, next, sizes,jumper,total",
        total: 0
      },
      //收费渠道分布
      chargingChannelData: {},
      chargingChannelRes: false,
      //核查异常规则Top10分布
      abnormalRulesData: {},
      abnormalRulesRes: false,
      //回收率分布
      recoveryData: false,
      recoveryRes: false,
      //图表组件数据
      setOptionData: {},
    }
  },
  mounted() {
    this.getLabelData()
    this.getAbnormalRulesData()
    this.getChargingChannelData()
    this.getRecoveryData()
    window.addEventListener("resize", () => {
      if (this.setOptionData['收费渠道分布']) this.setOptionData['收费渠道分布'].resize()
      if (this.setOptionData['核查异常规则Top10分布']) this.setOptionData['核查异常规则Top10分布'].resize()
      if (this.setOptionData['回收率分布']) this.setOptionData['回收率分布'].resize()
    });
  },
  methods: {
    //图表组件
    setOption(name, val) {
      this.setOptionData[name] = val
    },
    changePage(data) {
      console.log(data, 'data')
      if (data.name === 'label') {
        this.labelQueryForm.size = data.size
        this.labelQueryForm.current = data.current
        this.getLabelData()
      }
      if (data.name === 'labelDetail') {
        this.labelDetailQueryForm.dbcolumnAS = data.keyValue || ''
        this.labelDetailQueryForm.size = data.size
        this.labelDetailQueryForm.current = data.current
        this.getLabelDetailData()
      }
    },
    //主题标签导出
    themeLabelExportExcel2(data) {
      try {
        exportExcelGet("/leanSeatingSkillController/exportLabelUserDisplay" + "?tagId=" + data.tagId);
      }finally {
        this.$refs.themeLabelList.changeLoading()
      }
    },
    //下面这个方法备用
    themeLabelExportExcel(datas) {
      this.excelLoading = true
      let path = '/leanSeatingSkillController/exportLabelUserDisplay'
      let data = {
        tagId: datas.tagId,
      }
      axios({
        method: "get",
        url: CP_PATH + path,
        params:data,
        headers: {
          'Content-Type': 'application/json;charset=UTF-8'
        },
        responseType: 'blob'
      }).then(res => {
        const content = res.data
        const blob = new Blob([content])
        const fileName = '导出信息.xls'
        if ('download' in document.createElement('a')) { // 非IE下载
          const elink = document.createElement('a')
          elink.download = fileName
          elink.style.display = 'none'
          elink.href = URL.createObjectURL(blob)
          document.body.appendChild(elink)
          elink.click()
          URL.revokeObjectURL(elink.href) // 释放URL 对象
          document.body.removeChild(elink)
        } else { // IE10+下载
          navigator.msSaveBlob(blob, fileName)
        }
      }).finally(()=>{
        this.$refs.themeLabelList.changeLoading()
      }).catch(()=>{
        this.$refs.themeLabelList.changeLoading()
      })
    },
    // 获取关注标签
    getLabelData() {

      this.themeLabelDisplay(this.labelQueryForm).then(res => {
        this.labelPageConfig.total = res.total
        this.labelPageConfig.size = this.labelQueryForm.size
        this.labelPageConfig.current = this.labelQueryForm.current
        this.labelData = res.list.map(e => {
          return {
            ...e,
            name: e.tagName,
            text: e.tagDescription,
            value: e.account || 0,
          }
        })

        console.log(this.labelData, 'labelData')

      })
    },
    //获取标签详情信息
    getLabelDetailData(data) {
      if (data) {
        this.labelDetailQueryForm.tagId = data.tagId
        this.labelDetailQueryForm.size = 10
        this.labelDetailQueryForm.current = 1
      }
      let columnNameData = []
      this.labelUserDisplay({ ... this.labelDetailQueryForm }).then(res => {
        this.labelDetailPageConfig.total = res.result[0] ? res.result[0].total : 0
        this.labelDetailPageConfig.size = this.labelDetailQueryForm.size
        this.labelDetailPageConfig.current = this.labelDetailQueryForm.current
        if (res.showField[this.labelDetailQueryForm.tagId]) {
          columnNameData = res.showField[this.labelDetailQueryForm.tagId].map(e => {
            return {
              prop: e.dbcolumnAS,
              label: e.dbcolumnTitle,
            }
          }).filter(e => e.label !== '地区编码' && e.label !== '县区局编码' && e.label !== '供电单位编码' && e.label !== '供电电网编码')
        }
        console.log(columnNameData, 'columnNameData')
        res.result.shift()
        this.labelDetailData = {
          tableData: [...res.result],
          columnNameData: columnNameData,
          name: "labelDetail",
          headerColor: '#1899fa',
          title: "客户明细列表",
          total: this.labelDetailPageConfig.total,
          current: this.labelDetailQueryForm.current,
          size: this.labelDetailQueryForm.size,

        }
        // console.log(this.labelDetailData, 'labelDetailData')

      })
    },
    //收费渠道分布
    getChargingChannelData() {
      this.chargingChannelRes = false
      this.chargingChannels({}).then(res => {
        console.log(res, 'chargingChannels')
        let dataMap1 = []
        let dataMap2 = []
        let xNameData = []
        res['month'].forEach(e => {
          dataMap1.push(e.count)
          xNameData.push(e.type)
        })
        res['year'].forEach(e => {
          dataMap2.push(e.count)
        })
        this.chargingChannelData = {
          dataMap: {
            '本年': dataMap2,
            '本月': dataMap1,
          },
          xNameData: xNameData,
          colorMap: {
            '本年': [
              // '#55BFFF', '#2790FF'
              ['#2790FF', '#55BFFF', '#ddf2ff'],
            ],
            '本月': [
              // '#55BFFF', '#2790FF'
              ['#19A7CD', '#66D6FF', '#dbf4fc'],
            ],

          },
          xyName: { 'x轴': { name: '', color: "#808080", diffcolor: false }, 'y轴': [{ name: '单位：万元', color: '#808080' }] }
        }
        this.chargingChannelRes = true
      })

    },
    // 核查异常规则Top10分布
    getAbnormalRulesData() {
      this.abnormalRulesRes = false
      this.verificationExceptionRulesTop10({}).then(res => {
        console.log(res, 'verificationExceptionRulesTop10')
        let dataMap = []
        let xNameData = []
        res.forEach(e => {
          dataMap.push(e.KHS)
          xNameData.push(e.DQ)
        });
        this.abnormalRulesData = {
          dataMap: {
            '核查异常规则Top10分布': dataMap
          },
          xNameData: xNameData,
          colorMap: {
            '核查异常规则Top10分布': [
              ['#29C644', '#8AE671',],
            ]
          },
          xyName: {
            'x轴': { name: '', color: "#808080", diffcolor: false }, 'y轴': [{ name: '户数', color: '#808080' }]
          }
        }
        this.abnormalRulesRes = true
      })

    },
    //回收率分布
    getRecoveryData() {
      this.recoveryRes = false

      this.rateOfRecovery({}).then(res => {
        let dataMap1 = []
        let dataMap2 = []
        let xNameData = []
        res['month'].forEach(e => {
          dataMap1.push(Math.round((e.percent * 100) * 1000) / 1000)
          xNameData.push(e.place)
        })
        res['year'].forEach(e => {
          dataMap2.push(Math.round((e.percent * 100) * 1000) / 1000)
        })
        this.recoveryData = {
          dataMap: {
            '本年': dataMap2,
            '本月': dataMap1,
          },
          xNameData: xNameData,
          colorMap: {
            '本年': [
              // '#55BFFF', '#2790FF'
              ['#5446B5', '#9688F9', '#eae7fd'],
            ],
            '本月': [
              // '#55BFFF', '#2790FF'
              ['#19CDAE', '#77E7BE', '#dff9f1'],
            ],
          },
          xyName: { 'x轴': { name: '', color: "#808080", diffcolor: false }, 'y轴': [{ name: '回收率%', color: '#808080' }] }
        }
        this.recoveryRes = true
      })
      this.$nextTick(() => {

      })
    },

  },
}
</script>

<style scoped lang="scss">
::v-deep .el-table .warning-row {
  // background: #e7f6f7;
  background: #e9f3fe;
}
.panoramicAnalysis {
  width: 100%;
  height: 100%;
  font-size: 14px;
  box-sizing: border-box;
  .top,
  .center,
  .bottom {
    width: 100%;
    // height: calc(50% - 10px);
    height: 300px;
    box-sizing: border-box;
    .content-border {
      background: #fff;
      border: 2px solid rgba(26, 132, 244, 0.2);
      border-radius: 4px;
    }
    .content {
      width: 100%;
      height: 100%;
      display: flex;
      flex-wrap: wrap;
      .content-title {
        display: flex;
        align-items: center;
        height: 30px;
        line-height: 30px;
        padding: 5px 0px 0px 5px;
        font-weight: bold;
        color: #1a84f4;
        .block {
          display: inline-block;
          width: 8px;
          height: 16px;
          margin: 0 10px;
          background-image: linear-gradient(270deg, #17a8ff 0%, #1a84f4 100%);
        }
      }
      .chart-main {
        width: 100%;
        height: calc(100% - 30px);
      }
      .content-left {
        width: calc(50% - 10px);
        margin-right: 10px;
        height: 100%;
      }
      .content-right {
        width: calc(50% - 10px);
        height: 100%;
      }
    }
  }
  .top {
    .content {
      width: calc(100% - 4px);
      margin: 0;
    }
    margin-bottom: 20px;
  }
  .center {
    margin-bottom: 20px;
  }
  .bottom {
    .content {
      width: calc(100% - 4px);
      margin: 0;
    }
    .content {
      display: flex;
      flex-wrap: wrap;
    }
  }
}
</style>