import ajax from '@/utils/ajax'

/**
 * 主题标签-标签展示
 * @const
 * @type {String}
 * @default  '[CP_PATH]/leanSeatingSkillController/themeLabelDisplay'
 */
export const THEME_LABEL_DISPLAY = CP_PATH + '/leanSeatingSkillController/themeLabelDisplay'

/**
 * 主题标签-标签展示
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function themeLabelDisplay(data) {
  return ajax({
    method: 'post',
    headers: {
      "Content-Type": "application/json;charset=utf8"
    },
    data: data,
    url: THEME_LABEL_DISPLAY
  })
}

